.container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
}

.container img{
    filter: blue;
}

.container p{
    color: #606060;

}

.container a{
    color: #009DFF;
}

.heading{
    padding-top: 15px;
    padding-left: 15px;
    font-size: 36px;
}

.sidebar{
    margin: 10px;
    background-color: white;
    padding-top: 25px;
    border-radius: 10px;  
    border: 1px solid #D7D7D7; 
}